.header {
  margin-top: 60px;
}
#top {
  background: red;
  width: 100vw;
  height: 1px;
  position: absolute;
  top: -300px;
}
.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Montserrat', serif;
  color: rgb(0, 80, 110);
}

.headerTitleSm {
  position: absolute;
  top: 18%;
  font-size: 20px;
}

.headerTitleLg {
  position: absolute;
  top: 20%;
  font-size: 100px;
}

.headerImg {
  width: 100%;
  height: 450px;
  margin-top: 80px;
  object-fit: cover;
}
