@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;700&family=Red+Hat+Display:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;700&family=Red+Hat+Display:wght@300;700&display=swap);
.top {
  width: 100%;
  height: 70px;
  background-color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  z-index: 999;
  font-family: 'Montserrat', sans-serif;
  box-shadow: rgba(100, 100, 111, 0.3) 0px 3px 10px 0px;
}

.topLeftlogo {
  width: 100px;
  height: auto;
  margin: 100px 0px;
}
.topLeft {
  flex: 3 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.topRight {
  flex: 3 1;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.topIcon {
  font-size: 20px;
  margin-right: 10px;
  color: rgb(0, 80, 110);
  cursor: pointer;
}

.topCenter {
  flex: 6 1;
}

.topList {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.linklogo {
  height: auto;
  cursor: pointer;
}
.topListItem {
  margin-right: 20px;
  font-size: 18px;
  font-weight: 400;
  cursor: pointer;
}
.topListanchor,
.topListItem {
  text-decoration: none;
  transition: all 1s;
  color: rgb(0 80 110);
}
.topListItem:hover {
  color: gray;
}
.topListanchor:hover {
  color: gray;
}

.topImg {
  width: 70px;
  height: 70px;
  object-fit: cover;
  margin-right: 15px;
  cursor: pointer;
}
.antimover {
  position: relative;
  width: 70px;
  height: 70px;
}
.topSearchIcon {
  top: 23px;
  left: 21px;
  position: absolute;
  font-size: 28px;
  color: rgb(0, 80, 110);
  cursor: pointer;
  transition: all 1s;
}
.topSearchIcon:hover {
  top: 18px;
  left: 16px;
  font-size: 38px;
  color: orange;
  cursor: pointer;
}
.topimg:hover {
  box-shadow: rgba(100, 100, 111, 0.6) 10px 70px 29px 0px;
  cursor: pointer;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Montserrat', serif;
  color: rgb(0, 80, 110);
}

.kaizenTitleSm {
  position: absolute;
  color: rgb(0, 80, 110);
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: calc(4px + 0.8vw);
  line-height: 2.8vw;
  text-align: center;
  width: 35%;
}

.headerTitleLg {
  position: absolute;
  font-size: 100px;
}

.kaizenImg {
  height: auto;
  width: 110vw;
  object-fit: cover;
}
.kaizen {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header {
  margin-top: 60px;
}
#top {
  background: red;
  width: 100vw;
  height: 1px;
  position: absolute;
  top: -300px;
}
.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Montserrat', serif;
  color: rgb(0, 80, 110);
}

.headerTitleSm {
  position: absolute;
  top: 18%;
  font-size: 20px;
}

.headerTitleLg {
  position: absolute;
  top: 20%;
  font-size: 100px;
}

.headerImg {
  width: 100%;
  height: 450px;
  margin-top: 80px;
  object-fit: cover;
}

.read-or-hide {
  color: rgba(0, 80, 110, 0.5);
  text-decoration: underline;
}

.postwrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
  max-width: 1360px;
}
.post {
  width: 395px;
  margin: 0px 20px 40px 20px;
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  align-items: center;
  justify-content: center;
  transition: box-shadow 1s;
  padding: 0px 0px 30px 0px;
}
.post:hover {
  box-shadow: rgba(100, 100, 111, 0.6) 0px 7px 29px 0px;
  cursor: pointer;
}

.postImg {
  width: 395px;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-weight: 700;
  font-size: 11px;
  color: #be9656;
  line-height: 19px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.postTitle {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 700;
  margin-top: 15px;
  cursor: pointer;
}
.postDate {
  font-family: 'Montserrat', serif;
  font-style: italic;
  font-size: 13px;
  font-weight: 400;
  color: rgb(0, 80, 110);
  margin-top: 15px;
}

.postDesc {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: rgb(0, 80, 110);
  margin: 15px 0px 0px 10px;
  /* overflow: hidden;
  text-overflow: ellipsis; */
  text-align: center;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  width: 95%;
}

.modalstyles {
  position: fixed;
  top: 50%;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: beige;
  width: 80vmin;
  height: 80vmin;
  z-index: 1000;
}
.modalImg {
  width: 80vmin;
  height: 40vmin;
  object-fit: cover;
}

#btn {
  position: absolute;
  float: right;
  margin: -2vmin 0vmin 0vmin 80vmin;
  color: white;
  background: rgba(0, 60, 110, 0.7);
  border-radius: 50%;
  width: 4vmin;
  height: 4vmin;
  text-align: center;
  box-shadow: 4px 2px 50px 2px white;
  transition: all 1s;
}
#btn:hover {
  box-shadow: 4px 2px 5px 2px white;
  cursor: pointer;
}
#modCont {
  display: flex;
  justify-content: center;
}
.modalBodyCont {
  width: 75vmin;
  margin: 4vmin 0vmin 0vmin 2.5vmin;
}
.grayBody {
  color: rgba(0, 80, 110, 0.5);
  text-align: center !important;
}
.blueBody {
  color: rgba(0, 80, 110, 1) !important;
  font-weight: 600;
}

.modalBody {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 2vmin;
  line-height: 3vmin;
}
.spacer {
  margin-top: 1.5vmin;
}


.home {
  display: flex;
  flex-direction: column;
  align-items: center;
}

